.App {
  text-align: center;
}

.app-box{
  border: 1px solid rgb(0, 0, 0);
  max-width: 45em;
  margin: 0 auto;
  padding-left: 1.5em;
  padding-right: 1.5em;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.coin-list{
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(max(8em, 10em), 1fr));
  gap: 0.25em;
}
